<!-- AddStudentForm.vue -->
<template>
    <div>
      <h2>Add Student</h2>
      <form @submit.prevent="onSubmit">
        <label for="studentName">Name:</label>
        <input type="text" id="studentName" v-model="newStudentName" required>
        <button type="submit">Add</button>
      </form>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        newStudentName: '',
      };
    },
    methods: {
      onSubmit() {
        this.$emit('addStudent', this.newStudentName);
        this.newStudentName = '';
      },
    },
  };
  </script>
  