<template>
  <div>
    <student-list :students="students" @addStudent="addStudent"></student-list>
    <add-student-form @addStudent="addStudent"></add-student-form>
  </div>
</template>

<script>
import StudentList from './components/StudentList.vue';
import AddStudentForm from './components/AddStudentForm.vue';

export default {
  components: {
    StudentList,
    AddStudentForm,
  },
  data() {
    return {
      students: [],
    };
  },
  methods: {
  addStudent(name) {
    // Construire le corps de la requête
    const requestBody = JSON.stringify({ name });

    // Envoyer la requête au backend
    fetch('http://localhost:8080/add_student', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: requestBody,
    })
    .then(response => response.json())
    .then(data => {
      // Mettez à jour this.students avec la nouvelle liste d'étudiants
      this.students.push(data);
    })
    .catch(error => {
      console.error('Erreur lors de l\'ajout de l\'étudiant :', error);
    });
  },
},
};
</script>
