<!-- front-end/src/components/StudentList.vue -->
<template>
    <div>
      <h2>Student List</h2>
      <ul>
        <li v-for="student in students" :key="student.id">
          {{ student.name }}
          <input type="checkbox" v-model="student.present" @change="onCheckboxChange(student)">
        </li>
      </ul>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      students: Array,
    },
    methods: {
      onCheckboxChange(student) {
        // Envoyer une requête au backend pour mettre à jour le statut de présence
        // Vous pouvez utiliser la même approche que dans la méthode addStudent
        fetch(`http://localhost:8080/mark_attendance/${student.id}/${student.present ? 'present' : 'absent'}`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
        })
        .then(response => response.json())
        .then(updatedStudent => {
          // Mettez à jour les détails de l'étudiant après la réponse du backend
          Object.assign(student, updatedStudent);
        })
        .catch(error => {
          console.error('Erreur lors de la mise à jour de la présence de l\'étudiant :', error);
        });
      },
    },
  };
  </script>
  
  
  